<!-- 添加学生 -->
<template>
  <section class="add">
    <el-form ref="form" :model="form" :rules="rules2" label-width="80px">
      <el-form-item label="姓名" prop="studentName">
        <el-input v-model="form.studentName"></el-input>
      </el-form-item>
      <!-- <el-form-item label="性别" prop="sex">
        <el-input v-model="form.sex"></el-input>
      </el-form-item> -->
      <el-form-item label="性别" prop="sex" :label-width="formLabelWidth">
        <el-select v-model="form.sex" placeholder="请选择性别">
          <el-option label="男" value="男"></el-option>
          <el-option label="女" value="女"></el-option>
        </el-select>
      </el-form-item>
      <!-- <el-form-item label="学院">
        <el-input v-model="form.institute"></el-input>
      </el-form-item> -->
      <!-- <el-form-item label="所属专业" prop="major">
        <el-input v-model="form.major"></el-input>
      </el-form-item> -->
      <el-form-item label="专业" prop="major" :label-width="formLabelWidth">
        <el-select v-model="form.major" placeholder="请选择专业">
          <el-option
            label="计算机科学与技术"
            value="计算机科学与技术"
          ></el-option>
          <el-option label="信息工程" value="信息工程"></el-option>
          <el-option label="网络工程" value="网络工程"></el-option>
          <el-option label="软件工程" value="软件工程"></el-option>
        </el-select>
      </el-form-item>
      <!-- <el-form-item label="年级" prop="grade">
        <el-input v-model="form.grade"></el-input>
      </el-form-item> -->
<!--      <el-form-item label="年级" prop="grade" :label-width="formLabelWidth">-->
<!--        <el-select v-model="form.grade" placeholder="请选择年级">-->
<!--          <el-option label="2017" value="2017"></el-option>-->
<!--          <el-option label="2018" value="2018"></el-option>-->
<!--        </el-select>-->
<!--      </el-form-item>-->
<!--      <el-form-item label="班级" prop="clazz" :label-width="formLabelWidth">-->
<!--        <el-select v-model="form.clazz" placeholder="请选择班级">-->
<!--          <el-option label="1班" value="1"></el-option>-->
<!--          <el-option label="2班" value="2"></el-option>-->
<!--        </el-select>-->
<!--      </el-form-item>-->
      <el-form-item label="年级" prop="grade">
        <el-select v-model="form.grade" placeholder="请选择年级">
          <el-option label="2019" value="2019"></el-option>
          <el-option label="2020" value="2020"></el-option>
          <el-option label="2021" value="2021"></el-option>
          <el-option label="2022" value="2022"></el-option>
        </el-select>
      </el-form-item>
      <el-form-item label="班级" prop="clazz">
        <el-select v-model="form.clazz" placeholder="请选择班级">
          <el-option label="1班" value="1"></el-option>
          <el-option label="2班" value="2"></el-option>
          <el-option label="3班" value="3"></el-option>
          <el-option label="4班" value="4"></el-option>
          <el-option label="5班" value="5"></el-option>
          <el-option label="6班" value="6"></el-option>
          <el-option label="7班" value="7"></el-option>
          <el-option label="8班" value="8"></el-option>
          <el-option label="9班" value="9"></el-option>
          <el-option label="10班" value="10"></el-option>
        </el-select>
      </el-form-item>
      <!-- <el-form-item label="班级" prop="clazz">
        <el-input v-model="form.clazz"></el-input>
      </el-form-item> -->
      <el-form-item label="电话号码" prop="tel">
        <el-input v-model="form.tel"></el-input>
      </el-form-item>
      <el-form-item label="身份证号" prop="cardId">
        <el-input v-model="form.cardId"></el-input>
      </el-form-item>
      <el-form-item label="邮箱" prop="email">
        <el-input v-model="form.email"></el-input>
      </el-form-item>
      <el-form-item label="密码" prop="pwd">
        <el-input v-model="form.pwd"></el-input>
      </el-form-item>
      <el-form-item>
        <el-button type="primary" @click="onSubmit('form')">立即创建</el-button>
        <el-button type="primary" @click="cancel('form')">重置</el-button>
      </el-form-item>
    </el-form>
  </section>
</template>

<script>
export default {
  data() {
    return {
      form: {
        //表单数据初始化
        studentName: null,
        grade: null,
        major: null,
        clazz: null,
        institute: "计算机与电气工程学院",
        tel: null,
        email: null,
        pwd: null,
        cardId: null,
        sex: null,
        role: 2
      },
      rules2: {
        studentName: [
          { required: true, message: "姓名不能为空", trigger: "blur" }
        ],
        pwd: [
          { required: true, message: "密码不能为空", trigger: "blur" },
          { min: 6, message: "密码不能少于6位字符", trigger: "blur" }
        ],
        grade: [
          { required: true, message: "请选择您的年级", trigger: "change" }
        ],
        major: [
          { required: true, message: "请选择您的专业", trigger: "change" }
        ],
        clazz: [
          { required: true, message: "请选择您的班级", trigger: "change" }
        ],
        tel: [{ required: true, message: "联系方式不能为空", trigger: "blur" }],
        email: [
          { required: true, message: "邮箱不能为空", trigger: "blur" },
          { type: "email", message: "邮箱格式不正确", trigger: "blur" }
        ],
        cardId: [
          { required: true, message: "身份证号不能为空", trigger: "blur" },
          { max: 18, message: "请输入正确的身份证号", trigger: "blur" }
        ],
        sex: [{ required: true, message: "请选择您的性别", trigger: "change" }]
      }
    };
  },
  methods: {
    onSubmit(formName) {
      //数据提交
      this.$refs[formName].validate(valid => {
        if (valid) {
          this.$axios({
            url: "/api/student",
            method: "post",
            data: {
              ...this.form
            }
          }).then(res => {
            if (res.data.code == 200) {
              this.$message({
                message: "数据添加成功",
                type: "success"
              });
              this.$router.push({ path: "/studentManage" });
            }
          });
        } else {
          return false;
        }
      });
    },
    cancel(formName) {
      //取消按钮
      // this.form = {};
      this.$refs[formName].resetFields();
    }
  }
};
</script>
<style lang="scss" scoped>
.add {
  padding: 0px 40px;
  width: 400px;
}
</style>



